import React from 'react';
import rainy from './images/Rainy.PNG';
import snowy from './images/Snowy.PNG';
import cloudy from './images/cloudy.jpg';
import clear from './images/Clear.jpeg';
import storm from './images/Thunderstorm.jpg';
import drizzle from './images/Drizzle.jpg';
import fog from './images/Fog.png';

/* Styles  */
const outline = "1px 1px #000000, -1px 1px #000000, 1px -1px #000000, -1px -1px #000000";

const textStyle = {
	fontFamily: "Arial",
	color: "white",
	textShadow: outline,
	fontWeight: 300,
	margin: "10px",
	fontSize: "calc(14px + (30 - 14) * ((100vw - 300px) / (1600 - 300)))"
};
const highText = {
	fontFamily: "Arial",
	color: "orange",
	textShadow: outline,
	fontWeight: 200,
	fontSize: "calc(14px + (30 - 14) * ((100vw - 300px) / (1600 - 300)))"
};

const lowText = {
	fontFamily: "Arial",
	color: "skyblue",
	textShadow: outline,
	fontWeight: 200,
	fontSize: "calc(14px + (30 - 14) * ((100vw - 300px) / (1600 - 300)))"
};

function getBgImg(condition) {
		if (condition === "Clear") return clear;
		if (condition === "Clouds") return cloudy;
		if (condition === "Rain") return rainy;
		if (condition === "Snow") return snowy;
		if (condition === "Thunderstorm") return storm;
		if (condition === "Drizzle") return drizzle;
		return fog;
	}
class WeatherCard extends React.Component {
	constructor(props) {
		super(props);
		this.state = {name: "",
		high: 0,
		low: 0,
		cond: "",
		bg: "",
		};
	}
	static getDerivedStateFromProps(props, state) {
		let img = getBgImg(props.condition);
		return {name: props.day,
			high: "High: " + props.high + "°C",
			low: "Low: " + props.low + "°C",
			cond: props.condition,
			bg: {
				backgroundSize: "cover",
				backgroundImage: "url(" + img + ")",
				backgroundColor: "#F5F6FA",
				width: "calc(100% - 12px)",
				float: "left",
				height: "35vw",
				textAlign: "center",
				border: "4px solid black",
				marginLeft: "6px"
			}
		};
	}
	Redirect = () => {
    window.location = window.location + "/" + this.state.name;
  }
	render(){
		return <div onClick={this.Redirect} style={this.state.bg}>
		<h2 style={textStyle}>{this.state.name}</h2>
		<h2 style={textStyle}>{this.state.cond}</h2>
		<h2 style={highText}>{this.state.high}</h2>
		<h2 style={lowText}>{this.state.low}</h2>
		</div>
	};
}

export default WeatherCard

